<template>
	<div class="create">
		<!-- <mt-header title="个人信息" class="seeInfo">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link> -->
			<!-- <router-link to="/editInfo" slot="right" class="edit">编辑</router-link> -->
		<!-- </mt-header> -->
		<div class="inform-photo">
			<img :src="informateList.wx_avatar" slot="center" v-if="informateList.wx_avatar">
			<img src="../../assets/photo.png" slot="center" v-else>
		</div>
		<div class="info-el">
			<el-row>
  				<el-col :span="8">账号</el-col>
  				<el-col :span="12">{{informateList.username}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">姓名</el-col>
  				<el-col :span="12">{{informateList.truename}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">性别</el-col>
  				<el-col :span="12">{{informateList.sex | sexFilter}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">年龄</el-col>
  				<el-col :span="12">{{informateList.age}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">民族</el-col>
  				<el-col :span="12">{{informateList.nation}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">证件类型</el-col>
  				<el-col :span="12">{{informateList.id_type | id_typeFilter}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">证件号码</el-col>
  				<el-col :span="12">{{informateList.id_code}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">血型</el-col>
  				<el-col :span="12">{{informateList.blood_type | blood_typeFilter}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">子女数</el-col>
  				<el-col :span="12">{{informateList.children | childrenFilter}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">兴趣爱好</el-col>
  				<el-col :span="12">{{informateList.hobby_list}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">特长</el-col>
  				<el-col :span="12">{{informateList.speciality_list}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">学历</el-col>
  				<el-col :span="12">{{informateList.education | educationFilter}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">职业</el-col>
  				<el-col :span="12">{{informateList.job_list}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">职务</el-col>
  				<el-col :span="12">{{informateList.duty}}</el-col>
			</el-row>
			<el-row>
  				<el-col :span="8">职称</el-col>
  				<el-col :span="12">{{informateList.skill_level_list}}</el-col>
			</el-row>
		</div>
		<div class="next save">
			<mt-button class="next-btn" @click="editClick">编辑</mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				informateList:{}
			}
		},
		created (){
			this.informateList = JSON.parse(window.localStorage.memberList);

		},
		filters: {
			sexFilter:function (input) {
				switch(input){
					case 1: return '女';
					case 0: return '男';
				}
			},
			id_typeFilter:function (input) {
				switch(input){
					case 0: return '身份证';
					case 1: return '护照';
					case 2: return '军官证';
					case 3: return '其他';
				}
			},
			blood_typeFilter:function (input) {
				switch(input){
					case 0: return 'A型';
					case 1: return 'B型';
					case 2: return 'AB型';
					case 3: return 'O型';
					case 4: return '其他';
				}
			},
			educationFilter:function (input) {
				switch(input){
					case 0: return '小学';
					case 1: return '初中';
					case 2: return '高中';
					case 3: return '学士';
					case 4: return '硕士';
					case 5: return '博士';
				}
			},
			childrenFilter:function (input) {
				switch(input){
					case 1: return '1';
					case 2: return '2';
					case 3: return '3';
					case 4: return '其他';
				}
			}
		},
		methods: {
			editClick (){
				window.localStorage.informateList = JSON.stringify(this.informateList);
				this.$router.push('/editInfo');
			}
		},
		
	}
</script>
<style lang="scss">
	.inform-photo{
		width: 65px;
		margin: 0 auto;
		margin-top: 20px;
		overflow: hidden;
	}
	.inform-photo img{
		width: 65px;
		height: 65px;
		border-radius: 50%;
		background-color: #f0f0f0;
	}
	.info-el{
		padding: 20px 15px;
		.el-row{
			padding:7px 5px;
			font-size:16px;
			color: #323232;
		}
	}
	.seeInfo .mintui{
		font-size: 20px;
	}
	.seeInfo .edit{
		text-decoration: none;
		font-size: 16px;
	}
</style>



